.left-panel-main{
  width: 15%;
  background-color: #444;
  float: left;
  height: 100vh;
}
.nav-li{
  color: white;
  text-align: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 800;
  border-bottom: 2px solid white;

  -webkit-transition: 1s;

  -o-transition: 1s;

  transition: 1s;
}
.nav-li-top{
  border-top: 2px solid white;
}
.nav-container{
  margin-top: 90%;
}
.nav-li:hover{
  cursor: pointer;
}
.clickable:hover{
  background-color: rgba(255,255,255,.5)
}
.nav-pers,
.nav-prof{
  font-size: 12px;
  font-weight: normal;
  padding: 3px;
  display: none;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.nav-pers-container:hover .nav-pers{
  display:block;
}
.nav-prof-container:hover .nav-prof{
  display: block;
}


